import { isGroup } from '@mintlify/common';
import { DecoratedNavigation, DecoratedNavigationEntry } from '@mintlify/models';
import { DecoratedNavigationConfig, divisions } from '@mintlify/validation';

import { checkNavAccess } from './checkNavAccess';

function filterDecoratedNavigationEntryByGroups<T extends DecoratedNavigationEntry>(
  entry: T,
  userGroups: Set<string>,
  userIsAuthenticated: boolean
): T | undefined {
  if (isGroup(entry)) {
    const subEntries = entry.pages
      .map((subEntry) =>
        filterDecoratedNavigationEntryByGroups(subEntry, userGroups, userIsAuthenticated)
      )
      .filter((subEntry) => subEntry !== undefined);

    if (subEntries.length) {
      return {
        ...entry,
        pages: subEntries,
      };
    }
    return undefined;
  }

  if (!userIsAuthenticated && !(entry.isPublic || entry.public)) return undefined;
  if (!checkNavAccess(entry, userGroups)) return undefined;

  return entry;
}

export function filterDecoratedNavigationByGroups(
  decoratedNav: DecoratedNavigation | undefined = [],
  userGroups: Set<string>,
  userIsAuthenticated: boolean
) {
  return decoratedNav
    .map((group) => filterDecoratedNavigationEntryByGroups(group, userGroups, userIsAuthenticated))
    .filter((group) => group !== undefined);
}

export function filterDocsDecoratedNavigationByGroups(
  docsDecoratedNav: DecoratedNavigationConfig,
  userGroups: Set<string>,
  userIsAuthenticated: boolean
): DecoratedNavigationConfig {
  return filterOnlyPublicPagesRecursive(docsDecoratedNav, userGroups, userIsAuthenticated);
}

function filterOnlyPublicPagesRecursive(
  nav: DecoratedNavigationConfig,
  userGroups: Set<string>,
  userIsAuthenticated: boolean
): DecoratedNavigationConfig {
  if ('pages' in nav) {
    return {
      ...nav,
      pages: nav.pages
        .map((page) =>
          filterDecoratedNavigationEntryByGroups(page, userGroups, userIsAuthenticated)
        )
        .filter((page) => !!page),
    };
  }

  let newNav = { ...nav };

  for (const division of ['groups', ...divisions]) {
    if (division in newNav) {
      const items = newNav[division as keyof typeof newNav] as DecoratedNavigationConfig[];
      newNav = {
        ...newNav,
        [division]: items.map((item) =>
          filterOnlyPublicPagesRecursive(item, userGroups, userIsAuthenticated)
        ),
      };
    }
  }

  return newNav;
}
