import { ENDPOINT, getLeavesClient, HEADERS } from '@mintlify/http-client';
import { importSPKI, jwtVerify } from 'jose';
import { cookies } from 'next/headers';
import { NextRequest, NextResponse } from 'next/server';

import { END_USER_AUTH_COOKIE_KEY } from '@/constants';
import { HOST_NAME, NEXT_PUBLIC } from '@/env';
import { validateUserInfo } from '@/hooks/useUserInfo/utils';
import {
  filterDecoratedNavigationByGroups,
  filterDocsDecoratedNavigationByGroups,
} from '@/utils/nav/filterDecoratedNavigationByGroups';

export const dynamic = 'force-dynamic';

export async function GET(req: NextRequest) {
  if (!NEXT_PUBLIC.AUTH_ENABLED || !process.env.END_USER_AUTH_PUBLIC_KEY) {
    return NextResponse.json(
      {
        error: 'Unauthorized',
        message: 'Authentication required',
      },
      { status: 401 }
    );
  }

  const providedHost = req.nextUrl.host;
  const trimmedHost = providedHost.replace('.' + HOST_NAME, '');

  if (!trimmedHost) {
    return new NextResponse(null, { status: 500 });
  }

  const { navigation: fullNav, docsDecoratedNav } = await getLeavesClient(trimmedHost, ENDPOINT, {
    headers: HEADERS,
    next: {
      tags: [trimmedHost],
    },
  }).getNavigation();

  const jwt = cookies().get(END_USER_AUTH_COOKIE_KEY)?.value;
  if (!jwt) {
    return NextResponse.json(
      {
        error: 'Unauthorized',
        message: 'Authentication required',
      },
      { status: 401 }
    );
  }

  try {
    const publicKey = await importSPKI(process.env.END_USER_AUTH_PUBLIC_KEY, 'EdDSA');
    const { payload } = await jwtVerify(jwt, publicKey);
    if (payload.userInfo && validateUserInfo(payload.userInfo)) {
      if (payload.host !== providedHost) {
        return NextResponse.json(
          {
            error: 'Unauthorized',
            message: 'Authentication required',
          },
          { status: 401 }
        );
      }
      const userGroups = new Set(payload.userInfo.groups ?? []);
      return NextResponse.json({
        navigation: filterDecoratedNavigationByGroups(fullNav, userGroups, true),
        docsDecoratedNav: docsDecoratedNav
          ? filterDocsDecoratedNavigationByGroups(docsDecoratedNav, userGroups, true)
          : undefined,
      });
    }
  } catch (error) {
    return NextResponse.json(
      {
        error: 'Unauthorized',
        message: 'Authentication required',
      },
      { status: 401 }
    );
  }

  return NextResponse.json(
    {
      error: 'Unauthorized',
      message: 'Invalid JWT',
    },
    { status: 401 }
  );
}
